// utils/message.js
import { Message } from 'element-ui'

const message = {
  success(text, duration = 3000) {
    Message({
      message: text,
      type: 'success',
      duration: duration,
      showClose: true,
      customClass: 'custom-message'
    })
  },

  error(text, duration = 3000) {
    Message({
      message: text,
      type: 'error',
      duration: duration,
      showClose: true,
      customClass: 'custom-message'
    })
  },

  warning(text, duration = 3000) {
    Message({
      message: text,
      type: 'warning',
      duration: duration,
      showClose: true,
      customClass: 'custom-message'
    })
  },

  info(text, duration = 3000) {
    Message({
      message: text,
      type: 'info',
      duration: duration,
      showClose: true,
      customClass: 'custom-message'
    })
  },

  // 
  handleResponse(response, successMsg = '') {
    if (response.data.status) {
      this.success(successMsg)
      return true
    } else {
      this.error(response.data.message || '')
      return false
    }
  },

  // 
  handleError(error) {
    if (error.response) {
      // 
      this.error(error.response.data.message || '')
    } else if (error.request) {
      // 
      this.error('')
    } else {
      // 
      this.error('')
    }
  }
}

export default message